import { INCREASE_COUNT, ADD_TO_CART, CLEAR_ALL_CART, LOGIN_OK, LOG_OUT } from './mutations-types'

import { updateCart, delCart } from "network/user";

export default {
  // 单一性，每一个函数只执行单一操作
  [INCREASE_COUNT](state, payload) {
    payload.count++;
    if (!state.isLogin) {
      localStorage.setItem(payload.iid, JSON.stringify(payload));
    }
  },
  [ADD_TO_CART](state, payload) {
    payload.checked = true;
    if (payload.first) {
      return state.cartList.push(payload);
    }
    if (state.isLogin) {
      updateCart(state.auth, payload.iid).catch((err) => {
        console.warn(err);
      })
    } else {
      state.cartList.push(payload);
      localStorage.setItem(payload.iid, JSON.stringify(payload));
    }
  },
  [CLEAR_ALL_CART](state) {
    state.cartList = [];
    if (state.isLogin) {
      delCart(state.auth).catch((err) => {
        console.warn(err);
      })
    } else {
      localStorage.clear();
    }
  },
  [LOGIN_OK](state, payload) {
    state.isLogin = true;
    state.email = payload.email;
    state.auth = payload.token;
    localStorage.setItem("isLogin", "true");
    localStorage.setItem("email", payload.email);
    localStorage.setItem("auth", payload.token);
  },
  [LOG_OUT](state) {
    state.auth = "";
    state.email = "";
    state.isLogin = false;
    state.cartList = [];
    localStorage.removeItem("auth");
    localStorage.removeItem("email");
    localStorage.removeItem("isLogin");
  }
}