export default {
  cartLength(state) {
    // getters 是个属性
    return state.cartList.length;
  },
  cartList(store) {
    return store.cartList;
  },
  getLoginStatus(store) {
    return store.isLogin;
  },
  getEmail(store) {
    return store.email;
  },
  getAuth(store) {
    return store.auth;
  }
}