import { user } from "./request";
import qs from "qs";

export function register(email, password) {
    return user({
        url: "register",
        method: "post",
        data: qs.stringify({
            email,
            password
        }),
        headers: { 'content-type': 'application/x-www-form-urlencoded' },
    })
}

export function login(email, password) {
    return user({
        url: "login",
        method: "post",
        data: qs.stringify({
            email,
            password
        }),
        headers: { 'content-type': 'application/x-www-form-urlencoded' },
    })
}

export function getCart(auth) {
    return user({
        url: "/getCart",
        method: "get",
        headers: {
            "Authorization": auth
        }
    })
}

export function updateCart(auth, iid) {
    return user({
        url: "updateCart",
        method: "post",
        data: qs.stringify({
            iid
        }),
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
            "Authorization": auth
        }
    })
}

export function delCart(auth) {
    return user({
        url: "delCart",
        method: "delete",
        headers: {
            "Authorization": auth
        }
    })
}