import { INCREASE_COUNT, ADD_TO_CART, CLEAR_ALL_CART, LOGIN_OK,LOG_OUT } from './mutations-types'

export default {
  addCart(context, payload) {
    return new Promise((resolve, reject) => {
      for (const item of context.state.cartList) {
        if (item.iid === payload.iid) {
          context.commit(INCREASE_COUNT, item);
          resolve('增加一个该商品到购物车成功');
          return;
        }
      }
      payload.count = 1;
      context.commit(ADD_TO_CART, payload);
      resolve('添加到购物车成功');
      // 
    })
  },
  addDirectCart(context, payload) {
    return new Promise((resolve, reject) => {
      payload.first = true;
      context.commit(ADD_TO_CART, payload);
    })
  },
  clearAllCart(context) {
    return new Promise((resolve, reject) => {
      context.commit(CLEAR_ALL_CART);
      resolve("清空购物车成功")
    })
  },
  loginOK(context, payload) {
    return new Promise((resolve, reject) => {
      context.commit(LOGIN_OK, payload);
      resolve("登录成功")
    })
  },
  logOut(context, payload) {
    return new Promise((resolve, reject) => {
      context.commit(LOG_OUT);
      resolve("退出登录成功")
    })
  }
}